@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}

table.vehiculos {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.vehiculos thead {
  background-color: #71B100;
}
table.vehiculos thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.vehiculos .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  width: 20rem;
}